.background{
    padding-top: 435px;
    &__container{
        display: grid;
        grid-template-columns: 1fr 1fr;
        justify-content: space-between;
        gap: 74px;        
    }
    h4{
        font-size: 24px;
        line-height: 34px;
        margin-bottom: 11px;
    }
    p{
        font-size: 18px;
        line-height: 30px;
    }
    &__line{
        width: 100%;
        height: 2px;
        background-color: #DAD1CA;
        margin-top: 122px;
        grid-column: 1/3;
    }
}