.support{
    padding: 200px 0;
    background-color: #FEF9F2;
    
    span{
        color: #FF9C08;
    }
    &__heading{
        display: flex;
        align-items: center;
        justify-content: space-between;
    }
    &__text{
        font-weight: 700;
        font-size: 42px;
        line-height: 58px;
        width: 50%;
    }
    &__btns{
        display: flex;
        gap: 28px;
    }
    &__body{
        display: flex;
        gap: 28px;
        margin-top: 18px;
    }
    &__left{
        p{
            font-size: 18px;
            line-height: 30px;
            color-scheme: #6E6E6E;
            margin-bottom: 52px;
        }
        img{
            margin-bottom: 32px;
        }
    }
    
    &__right{
        p{
            font-size: 18px;
            line-height: 30px;
            color-scheme: #6E6E6E;
            margin-top: 50px;
            margin-bottom: 20px; 
        }
    }
    &__checkWrapper{
        display: flex;
        flex-direction: column;
        gap: 20px;
    }
    &__check{
        display: flex;
        align-items: center;
        gap: 16px;
    }
}