.footer {
    padding: 176px 0 28px;
    background-color: #FEF9F2;

    // &__container {
    //     // border-bottom: 1px solid #F0ECE8;
    // }

    &__left {
        p {
            font-size: 18px;
            line-height: 30px;
            margin-top: 26px;
        }
    }

    &__main {
        display: grid;
        grid-template-columns: repeat(3, 30%);
        justify-content: space-between;
        gap: 59px;
        padding-bottom: 110px;
        border-bottom: 1px solid #F0ECE8;
    }

    &__middle {
        display: flex;
        gap: 60px;

        ul {
            display: flex;
            flex-direction: column;
            gap: 22px;
        }

        strong {
            font-weight: 700;
            font-size: 20px;
            line-height: 22px;
        }

        a {
            color: #3F3F3F;
            font-size: 18px;
            line-height: 20px;
        }
    }

    &__right {
        background-color: #FF9C08;
        padding: 64px 48px 50px;
        border-radius: 14px;

        h3 {
            font-weight: 700;
            font-size: 24px;
            line-height: 34px;
        }

        button {
            width: 100%;
            padding: 23px 0 28px;
            margin-top: 20px;
            border-radius: 10px;
            text-align: center;
            cursor: pointer;
            font-weight: 700;
            font-size: 18px;
            line-height: 20px;
        }

        &-btn1 {
            color: white;
            background-color: #090909;
        }

        &-btn2 {
            color: #090909;
            background-color: white;
            border: none;
        }

    }

    &__bottom {
        display: flex;
        justify-content: space-between;
        padding-top: 37px;

        p {
            color: #6E6E6E;
            font-size: 18px;
            line-height: 30px;
            a{
                text-decoration: solid;
                color: #090909;
            }
        }

        &__rectangul {
            display: flex;
            gap: 12px;

            div {
                width: 36px;
                height: 36px;
                background-color: #090909;
                border-radius: 6px;
            }
        }
    }
}