.header {
    padding: 32px 0;
    border-bottom: 2px solid #ffffff;
    position: fixed;
    z-index: 5;
    width: 100%;
    background-color :#E5E5E5;

    &__container {
        display: flex;
        justify-content: space-between;
    }

    &__nav {
        display: flex;
        gap: 40px;
    }

    &__btns {
        display: flex;
        gap: 24px;
        align-items: center;
    }

    ul {
        display: flex;
        align-items: center;
        gap: 26px;
        list-style: none;

        a {
            text-decoration: none;
            color: #090909;
            font-size: 17px;
            line-height: 30px;
        }
    }

}