.testimonials {
    background-color: #FEF9F2;
    padding: 418px 0 252px;
    &__container {
        display: flex;
        align-items: center;
        gap: 108px;
    }

    &__left {
        position: relative;
    }

    &__article {
        padding: 68px 42px;
        position: absolute;
        width: 388px;
        height: 298px;
        right: -50px;
        bottom: -52px;

        background: #FFFFFF;
        border: 1px solid #F0ECE8;
        box-shadow: 0px 2px 12px rgba(43, 43, 43, 0.06);
        border-radius: 12px;

        p,
        q {
            font-size: 18px;
            line-height: 30px;
            color: #6E6E6E;
        }

        h5 {
            font-size: 18px;
            line-height: 20px;
            margin: 16px 0;
        }
    }

    &__right {
        &__heading {
            h5 {
                font-weight: 700;
                font-size: 20px;
                line-height: 22px;
                letter-spacing: 1.2px;
                text-transform: uppercase;
                color: #FF9C08;
            }

            h3 {
                font-weight: 700;
                font-size: 42px;
                line-height: 58px;
                margin: 20px 0 25px;
            }
        }

        &__body{
            p{
                color: #6E6E6E;
                font-size: 18px;
                line-height: 30px;
                margin-bottom: 12px;
            }
            margin-bottom: 50px;
        }
    }

    &__checkWrapper{
        display: flex;
        flex-direction: column;
        gap: 20px;
    }
    &__check{
        display: flex;
        align-items: center;
        gap: 16px;
    }

}