.video {
    background-color: #FFD4A4;
    padding: 200px 0 400px;

    &__container {
        display: flex;
        align-items: center;
        flex-direction: column;
        text-align: center;
        position: relative;

        p {
            font-weight: 400;
            font-size: 20px;
            line-height: 22px;
            letter-spacing: 1.2px;
            text-transform: uppercase;
        }
        h3{
            font-weight: 700;
            font-size: 42px;
            line-height: 58px;
            width: 60%;
            margin-top: 20px;
            margin-bottom: 30px;
        }
    }

    &__btns{
        display: flex;
        gap: 26px;
    }
    &__png{
        position: absolute;
        top: 311px;
    }
}