.assist{
    padding: 418px 0;

    &__container{
        position: relative;
    }
    span{
        color: #FF9C08;
    }
    &__head{
        display: flex;
        align-items: center;
        justify-content: space-between;
    }
    &__text{
        font-weight: 700;
        font-size: 42px;
        line-height: 58px;
        width: 50%;
    }
    &__btns{
        display: flex;
        gap: 28px;
    }
    &__body{
        margin-top: 62px;
        display: grid;
        grid-template-columns: 1fr 1fr 1fr;
        gap: 28px;
    }
}