.results {
    padding: 200px 0;

    &__container {
        display: flex;
        align-items: center;
        gap: 116px;
    }

    &__span {
        font-weight: 400;
        font-size: 20px;
        line-height: 22px;
        letter-spacing: 1.2px;
        color: #FF9C08;
        margin-bottom: 20px;
    }

    h4 {
        font-weight: 700;
        font-size: 42px;
        line-height: 58px;
        color: #090909;
    }

    &__cards {
        display: grid;
        grid-template-columns: 1fr 1fr;
        margin-top: 30px;
        gap: 28px 100px;
        margin-bottom: 72px;
    }

    &__card {
        display: flex;
        align-items: center;
        gap: 16px;

        h5 {
            font-weight: 700;
            font-size: 38px;
            line-height: 44px;

        }
        span {
            color: #6E6E6E;
        }
    }
}