.card{
    display: flex;
    flex-direction: column;
    align-items:center;
    padding: 57px 38px 61px;
    background: #FFFFFF;
    border: 1px solid #F0ECE8;
    box-shadow: 0px 2px 12px rgba(43, 43, 43, 0.06);
    border-radius: 12px;

    h5{
        font-size: 24px;
        line-height: 34px;
        font-weight: 700;
        margin: 24px 0 11px;
    }
    p{
        font-size: 18px;
        line-height: 30px;
    }
}