.intro {
    padding-top: 115px;
    &__container {
        display: flex;
        align-items: center;
        gap: 56px;
    }

    &__line {
        width: 140px;
        height: 4px;
        background-color: #FF9C08;
        border-radius: 99999px;
        margin-bottom: 25px;
    }

    &__heading {
        font-weight: 700;
        font-size: 55px;
        line-height: 80px;
        margin-bottom: 16px;
    }

    &__text {
        font-size: 24px;
        line-height: 38px;
        margin-bottom: 44px;
    }
    &__btns{
        display: flex;
        gap: 28px;
    }

}