.news {
    padding: 200px 0;

    &__container {
        display: flex;
        align-items: center;
        flex-direction: column;
    }

    h5 {
        font-weight: 700;
        font-size: 20px;
        line-height: 22px;
        text-align: center;
        letter-spacing: 1.2px;
        text-transform: uppercase;
        color: #FF9C08;
    }

    h3 {
        font-weight: 700;
        font-size: 42px;
        line-height: 58px;
        margin: 18px 0 50px;
    }

    &__body {
        display: flex;
        justify-content: space-between;
        gap: 28px;


        &__box {
            padding: 50px;
            border: 1px solid #F0ECE8;
            box-shadow: 0px 2px 12px rgba(43, 43, 43, 0.08);
            border-radius: 12px;
            width: 90%;
            margin-top: 30px;
            background-color: #FFFFFF;

            div:nth-child(1) {
                display: flex;
                align-items: center;
                gap: 18px;
                margin-bottom: 18px;

                button {
                    padding: 15px 23px;
                    border: 1px solid #F0ECE8;
                    border-radius: 6px;
                }
            }

            b {
                font-weight: 700;
                font-size: 24px;
                line-height: 34px;
            }

            div:nth-child(3) {
                margin-top: 32px;
                button {
                    text-align: start;
                    width: 93px;
                    height: 40px;
                    font-weight: 700;
                    font-size: 18px;
                    line-height: 20px;
                    border: none;
                    background-color: inherit;
                    cursor: pointer;
                }
            }
        }
    }
    &__footer{
        margin-top: 66px;
        display: flex;
        gap: 28px;
    }

}