.statistics {
    background-color: #FEF9F2;
    padding: 134px 0 73px 0;
    margin-top: -52px;
    &__container {
        display: flex;
        justify-content: space-between;
    }

    &__heading {
        font-weight: 700;
        font-size: 60px;
        line-height: 80px;

        b {
            color: #FF9C08;
            font-weight: 700;
            font-size: 60px;
            line-height: 80px;
        }
    }

    &__text {
        font-size: 20px;
        line-height: 22px;
        color: #6E6E6E;
    }
}