.join {
    position: absolute;
    background-color: #FF9C08;
    border-radius: 40px;
    padding: 160px 60px;
    margin-top: 200px;

    &__container {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }

    h5 {
        font-weight: 700;
        font-size: 42px;
        line-height: 58px;
    }

    &__btns {
        display: flex;
        gap: 30px;
        width: 100%;
        justify-content: end;

        button:nth-child(1) {
            padding: 26px 38px;
            color: white;
            background-color: #090909;
            border-radius: 10px;
            font-weight: 700;
            font-size: 18px;
            line-height: 20px;
            cursor: pointer;
        }

        button:nth-child(2) {
            padding: 26px 32px;
            background-color: white;
            border-radius: 10px;
            border: none;
            font-weight: 700;
            font-size: 18px;
            line-height: 20px;
            cursor: pointer;
        }
    }
}